import { Component, Host, h } from '@stencil/core';

@Component({
	tag: 'o-menu-label',
	styleUrl: 'o-menu-label.scss',
	shadow: true
})
export class OMenuLabel {
	render() {
		return (
			<Host>
				<span class={'inline-block title-sm text-content-default px-4 py-3'}>
					<slot></slot>
				</span>
			</Host>
		);
	}
}
