import { Component, Host, h } from '@stencil/core';

@Component({
	tag: 'o-skeleton',
	styleUrl: 'o-skeleton.scss',
	shadow: true
})
export class OSkeleton {
	render() {
		return (
			<Host>
				<div class="animate-pulse size-full bg-background-strong rounded-radius-lg min-h-4"></div>
			</Host>
		);
	}
}
