import type { EventEmitter } from '@stencil/core';
import { Component, Element, Event, h, Method } from '@stencil/core';
import type { OMenuItem } from '../o-menu-item/o-menu-item';

export interface OMenuSelectPayload {
	item: OMenuItem;
}

@Component({
	tag: 'o-menu',
	styleUrl: 'o-menu.scss',
	shadow: true
})
export class OMenu {
	private menu: HTMLSlotElement;
	private menuItems: OMenuItem[];
	private activeItem: OMenuItem;
	@Element() host!: HTMLOMenuElement;

	@Event() oMenuSelect!: EventEmitter<OMenuSelectPayload>;

	/* istanbul ignore next */
	handleClick = (event: MouseEvent) => {
		const composedPath = event.composedPath();
		const target = composedPath.find((el: Element) => el?.getAttribute?.('role') === 'menuitem');

		if (!target) return;

		(target as HTMLElement).blur();
		this.oMenuSelect.emit({ item: target as unknown as OMenuItem });
	};

	/* istanbul ignore next */
	handleMouseDown = (event: MouseEvent) => {
		const target = event.target as HTMLElement;

		if (this.isMenuItem(target)) this.setCurrentItem(target as unknown as OMenuItem);
	};

	/* istanbul ignore next */
	handleKeyDown = (event: KeyboardEvent) => {
		if (event.key === 'Enter' || event.key === ' ') {
			event.preventDefault();
			event.stopPropagation();

			if (this.activeItem.index === 0)
				(this.activeItem as unknown as HTMLElement).shadowRoot.querySelector('div').click();
		} else if (['ArrowDown', 'ArrowUp', 'Home', 'End'].includes(event.key)) {
			let index = this.activeItem ? this.menuItems.indexOf(this.activeItem) : 0;

			if (this.menuItems.length > 0) {
				event.preventDefault();
				event.stopPropagation();

				if (event.key === 'ArrowDown') {
					index++;
				} else if (event.key === 'ArrowUp') {
					index--;
				} else if (event.key === 'Home') {
					index = 0;
				} else if (event.key === 'End') {
					index = this.menuItems.length - 1;
				}

				if (index < 0) {
					index = this.menuItems.length - 1;
				}
				if (index > this.menuItems.length - 1) {
					index = 0;
				}

				this.setCurrentItem(this.menuItems[index]);
				this.menuItems[index].setFocus();
			}
		}
	};

	async handleSlotChange() {
		const items = await this.getMenuItems();
		this.menuItems = await this.getMenuItems();

		if (items.length > 0) this.setCurrentItem(items[0]);
	}

	isMenuItem(item: HTMLElement) {
		return item.tagName.toLowerCase() === 'o-menu-item';
	}

	async getCurrentItem() {
		return (await this.getMenuItems()).find((i) => i.index === 0);
	}

	@Method()
	async setCurrentItem(item: OMenuItem) {
		this.activeItem = item;
		const items = await this.getMenuItems();

		items.forEach((i) => {
			i.index = i === item ? 0 : -1;
		});
	}

	@Method()
	async getMenuItems() {
		return [...this.menu.assignedElements?.({ flatten: true })].filter((el: HTMLElement) => {
			if (el.inert || !this.isMenuItem(el)) {
				return false;
			}
			return true;
		}) as unknown as OMenuItem[];
	}

	render() {
		return (
			<div
				role="menu"
				onClick={this.handleClick}
				onMouseDown={this.handleMouseDown}
				onKeyDown={this.handleKeyDown}
				class="bg-background-default border-width-sm border-border-default rounded-radius-lg py-2"
			>
				<slot ref={(el) => (this.menu = el as HTMLSlotElement)} onSlotchange={() => this.handleSlotChange()}></slot>
			</div>
		);
	}
}
