import { Component, Host, h } from '@stencil/core';

@Component({
	tag: 'o-callout',
	styleUrl: 'o-callout.scss',
	shadow: true
})
export class OCallout {
	render() {
		return (
			<Host>
				<div class="bg-background-subtle p-4 rounded-radius-lg">
					<slot></slot>
				</div>
			</Host>
		);
	}
}
